@import url('https://fonts.googleapis.com/css?family=Bitter:400,700|Roboto:100,300');

html, body {
  font-family: 'Roboto', 'Helvetica Neue', 'Helvetica', sans-serif;
  font-weight: 300;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
}

@import 'media';
@import '404';

body.mdl-demo {
  background-color: white !important;
  background-image: url('/images/lace.png');
  background-position: 0% 98%;
  background-repeat: repeat-x;
  background-size: 360px;

  .mdl-layout {
    position: relative;
    background-image: url('/images/michael-sean-crates-mcdade.png');
    background-position: 0% 0%;
    background-repeat: no-repeat;
    background-size: 101px;

    &.is-small-screen {
      .mdl-layout__header-row h3 {
        font-size: inherit;
      }

      .mdl-layout__tab-bar .mdl-button {
        display: none;
      }

      .mdl-grid > .mdl-card {
        width: 100%;
      }
    }

    header, main {
      margin: 0 auto;
      max-width: 1440px;
    }

    @import 'header';
  }

  //.mdl-layout__header-row {
  //  padding-left: 40px;
  //  position: relative;
  //}



  .mdl-layout__tab-bar {
    padding: 0;
    padding-left: 16px;
    box-sizing: border-box;
    height: 100%;
    width: 100%;

    .mdl-layout__tab {
      height: 64px;
      line-height: 64px;

      &.is-active::after {
        background-color: white;
        height: 4px;
      }
    }
  }

  .mdl-layout__tab-panel {
    padding: 8px;
    padding-top: 20px;
  }

  .mdl-grid {
    background-color: #fcfcfc;
  }

  .mdl-card {
    height: auto;
    display: flex;
    flex-direction: column;
    background-color: #fcfcfc;
  }

  .mdl-card > * {
    height: auto;
  }

  .mdl-card .mdl-card__supporting-text {
    margin: 40px;
    flex-grow: 1;
    padding: 0;
    color: inherit;
    width: calc(100% - 80px);
  }

  .mdl-demo.mdl-card__supporting-text h4 {
    margin-top: 0;
    margin-bottom: 20px;
  }

  .mdl-card__actions {
    margin: 0;
    padding: 4px 40px;
    color: inherit;
  }

  .mdl-card__actions a {
    color: #00BCD4;
    margin: 0;
  }

  .mdl-card__actions a:hover,
  .mdl-card__actions a:active {
    color: inherit;
    background-color: transparent;
  }

  .mdl-card__supporting-text + .mdl-card__actions {
    border-top: 1px solid rgba(0, 0, 0, 0.12);
  }
  #add {
    position: absolute;
    right: 40px;
    top: 36px;
    z-index: 999;
  }

  .mdl-layout__content {
    section:not(:last-of-type) {
      position: relative;
      margin-bottom: 48px;
    }
  }

  section {
    &.section--center {
      max-width: 1240px;
    }

    &> header {
      display: flex;
      align-items: center;
      justify-content: center;

      &> .material-icons {
        font-size: 3rem;
      }
    }

    &> .section__play-btn {
      min-height: 200px;
    }

    &> button {
      position: absolute;
      z-index: 99;
      top: 8px;
      right: 8px;
    }

    .section__circle {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-grow: 0;
      flex-shrink: 1;
    }

    .section__text {
      flex-grow: 1;
      flex-shrink: 0;
      padding-top: 8px;

      h5 {
        font-size: inherit;
        margin: 0;
        margin-bottom: 0.5em;
      }

      a {
        text-decoration: none;
      }
    }

    .section__circle-container > .section__circle-container__circle {
      width: 64px;
      height: 64px;
      border-radius: 32px;
      margin: 8px 0;
    }

    &.section--center .section__text:not(:last-child) {
      border-bottom: 1px solid rgba(0, 0, 0, .13);
    }

    &.section--footer {
      padding: 64px 0;
      margin: 0 -8px -8px -8px;

      .section__circle--big {
        width: 100px;
        height: 100px;
        border-radius: 50px;
        margin: 8px 32px;
      }
    }
  }

  footer.mdl-mega-footer {
    background:  linear-gradient(to bottom, #c91a1a, #781d1d);
    font-size: 13px;
    margin-top: 20px;

    .mdl-mega-footer--heading, .mdl-mega-footer__heading {
      font-family: 'Bitter', 'Roboto', sans-serif;
      font-size: 16px;
      font-weight: 400;
      letter-spacing: 0.05em;
    }
  }

  //#features section.section--center {
  //  max-width: 620px;
  //}

  .is-small-screen section.section--footer .section__circle--big {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    margin: 8px 16px;
  }

  .mdl-card .mdl-card__supporting-text > h3:first-child {
    margin-bottom: 24px;
  }

  //.mdl-layout__tab-panel:not(#overview) {
  //  background-color: white;
  //}

  #features section {
    margin-bottom: 72px;
  }
  #features h4, #features h5 {
    margin-bottom: 16px;
  }

  .toc {
    border-left: 4px solid #C1EEF4;
    margin: 24px;
    padding: 0;
    padding-left: 8px;
    display: flex;
    flex-direction: column;
  }

  .toc h4 {
    font-size: 0.9rem;
    margin-top: 0;
  }

  .toc a {
    color: #4DD0E1;
    text-decoration: none;
    font-size: 16px;
    line-height: 28px;
    display: block;
  }

  .mdl-menu__container {
    z-index: 99;
  }
}
