@media (min-width: 1025px) and (max-width: 1300px) {
  html > body.mdl-demo {
    div#crates-media-logo {
      &:before {
        content: '';
        display: none;
        visibility: hidden;
        background: transparent;
      }
    }
  }
}

@media (min-width: 1025px) and (max-width: 1236px) {
  html > body.mdl-demo .mdl-layout {
    background-position: 95% 0%;

    div#crates-media-logo {
      display: none;
    }
  }
}

@media (min-width: 1236px) and (max-width: 1600px) {
  html > body.mdl-demo .mdl-layout {
    background-image: none;
  }
}

@media (min-width: 1025px) and (max-width: 1624px) {
  body.mdl-demo {
    .mdl-layout {
      background-size: 80px;
      background-position: 85% 0%;
    }
  }
}

@media (max-width: 1024px) {
  html > body.mdl-demo .mdl-layout {
    background-image: none;

    div#crates-media-logo {
      &:before {
        height: 140px;
      }

      img {
        height: 140px;
      }
    }
  }
}

@media (min-width: 801px) and (max-width: 1240px) {
  html > body.mdl-demo .mdl-layout {
    section.section--center {
      max-width: 840px;
    }
  }
}

@media (max-width: 800px) {
  html > body.mdl-demo .mdl-layout {
    section.section--center {
      max-width: 480px;
    }

    .hide-on-small-sizes {
      display: none;
    }
  }
}

@media (min-width: 800px) and (max-width: 905px) {
  html > body.mdl-demo {
    div#crates-media-logo {
      display: none;
      visibility: hidden;
    }
  }
}

@media (max-width: 650px) {
  html > body.mdl-demo {
    div#crates-media-logo {
      display: none;
      visibility: hidden;
    }
  }
}
