#crates-media-logo {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  z-index: 10;
  border-bottom: solid 1px white;

  &:before {
    content: ' ';
    background: linear-gradient(to right, transparent, rgba(255, 255, 255, 20), rgba(255, 255, 255, 80), white);
    width: 20px;
    left: -20px;
    height: 237px;
    position: absolute;
    display: block;
  }

  img {
    width: auto;
    height: 224px;
    border-bottom: solid 2px white;
  }
}

.mdl-layout__header {
  background-color: transparent !important;

  .mdl-layout__tab-bar-container {
    height: 64px;

    .mdl-layout__tab-bar {
      background: linear-gradient(to top, rgb(101, 25, 25), rgb(207, 26, 26)) !important;

      .mdl-layout__tab {
        &.is-active:after {
          background-color: red;
        }
      }
    }

    .mdl-layout__tab-bar-button {
      display: none;
    }
  }

  h1, h2, h3 {
    padding-left: 20px;
    margin: 0;
    opacity: 0.9;
    color: #c00;
  }

  h1, h2 {
    font-family: 'Bitter', 'Roboto', sans-serif;
  }

  h1 {
    font-weight: 700;
    letter-spacing: 0.02em;
    color: red;
  }

  h2 {
    font-weight: 400;
    font-size: 39px;
  }

  h3 {
    margin-top: -1px;
    letter-spacing: 0.07em;
    font-size: 29px;
    color: #900;
    font-weight: 100;
    font-family: 'Roboto', sans-serif;
  }
}

&.is-small-screen .mdl-layout__header {
  h1, h2, h3 {
    line-height: normal;
    opacity: 1;
  }

  h1 {
    font-weight: 400;
    font-size: 25px;
    letter-spacing: 0.08em;
  }

  h2 {
    font-weight: 400;
    font-size: 18px;
  }

  h3 {
    margin-top: 1px;
    font-size: 14px;
    letter-spacing: 0.05em;
  }
}

&:not(.is-small-screen) {
  .mdl-layout__tab-bar,
  .mdl-layout__tab-bar-container {
    overflow: visible;
  }

  .mdl-layout__header {
    height: 227px;

    h1, h2, h3 {
      overflow: hidden;
      white-space: nowrap;
    }

    h1 {
      height: 76px;
      font-size: 57px;
    }
  }
}
